import request from '@/utils/request'
import config from '@/common/config'

export function getHistoryTaskList(params) {
    return request({
        url: config.bpmnUrl + 'history/historic-task-instances',
        method: 'get',
        params
    })
}

export function getAHistoryTask(params) {
    return request({
        url: config.bpmnUrl + 'history/historic-task-instances/'+params,
        method: 'get',
        params
    })
}

export function getDetail(data) {
    return request({
        url: config.bpmnUrl + 'query/historic-variable-instances?size=1000',
        method: 'post',
        data
    })
}
